<template>
    <div class="join">
        <nav-bar
            v-if="isShowNavbar()"
            title="资料填写"
            class='title-bar'
            :webview="false"
            :hairline="false"/>
        <div class="form">
            <div class="form-item">
                <label for="name">姓名:</label>
                <fui-input
                    :label="form.name.label"
                    :label-key="form.name.labelKey"
                    :placeholder="form.name.placeholder"
                    v-model='form.name.value'
                    :required='form.name.required'
                    :valiedate-message="form.name.valiedateMessage"
                    :error-status='form.name.errorStatus'
                    @getFieldsError="errorCollect"
                />
            </div>
            <div class="form-item">
                <label for="telephone">手机号:</label>
                <fui-input
                    :label="form.phone.label"
                    :label-key="form.phone.labelKey"
                    :placeholder="form.phone.placeholder"
                    v-model='form.phone.value'
                    :required='form.phone.required'
                    :valiedate-message="form.phone.valiedateMessage"
                    :error-status='form.phone.errorStatus'
                    :hanle-validate='hanleValidate'
                    @getFieldsError="errorCollect"
                />
            </div>
            <div class="form-item">
                <label for="job">职业:</label>
                <div
                    :class="[
                        'select',
                        form.job.value?'':'select-disabled',
                        form.job.errorStatus?'feed-item-select-error':'']"
                    @click="showAction">
                    {{ form.job.value||'请选择职业' }}
                </div>
                <p
                    v-if="form.job.errorStatus"
                    class="feed-item-select-error-msg">{{ form.job.valiedateMessage }}</p>
                <van-actionsheet
                    v-model="form.job.show"
                    cancel-text="取消"
                    @cancel="onCancel"
                    :actions="form.job.actions"
                    @select="onSelect"
                />
            </div>
            <div class="form-item">
                <label for="telephone">我的梦想:</label>
                <fui-textarea
                    :label="form.dream.label"
                    :label-key="form.dream.labelKey"
                    :placeholder="form.dream.placeholder"
                    v-model='form.dream.value'
                    :required='form.dream.required'
                    :valiedate-message="form.dream.valiedateMessage"
                    :error-status='form.dream.errorStatus'
                    @getFieldsError="errorCollect"
                />
            </div>
            <div class="form-item">
                <label for="telephone">合伙人宣言:</label>
                <fui-textarea
                    :label="form.declaration.label"
                    :label-key="form.declaration.labelKey"
                    :placeholder="form.declaration.placeholder"
                    v-model='form.declaration.value'
                    :length='form.declaration.length'
                    :required='form.declaration.required'
                    :valiedate-message="form.declaration.valiedateMessage"
                    :error-status='form.declaration.errorStatus'
                    @getFieldsError="errorCollect"
                />
            </div>
        </div>
        <div class="join-tip">
            <div class="join-tip-title">
                温馨提示:
            </div>
            <div class="join-tip-content">
                {{ joinRemind }}
            </div>
        </div>
        <div class="join-footer">
            <div
                class="join-footer-btn"
                @click="check">
                立即提交
            </div>
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { isXiangkan } from '@/utils/ua';
import { joinPartner } from '@/api/partner';
import { isTakeOverBackByWeb, toast } from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import fuiInput from '@f2f/feed-ui/packages/input/index.vue';
import config from './config';


const fuiTextarea = fuiInput.fuiTextarea;

export default {
    name: 'JoinPartner',
    components: {
        fuiInput,
        fuiTextarea,
        NavBar,
    },
    data() {
        return {
            form: {
                name: {
                    label: '姓名',
                    labelKey: 'name',
                    placeholder: '请填写真实姓名',
                    value: '',
                    required: true,
                    errorStatus: false,
                    valiedateMessage: '输入不能为空',
                },
                phone: {
                    label: '手机号',
                    labelKey: 'phone',
                    placeholder: '请填写常用手机号码',
                    value: '',
                    required: true,
                    errorStatus: false,
                    valiedateMessage: '输入不能为空',
                },
                job: {
                    show: false,
                    value: '',
                    actions: [
                        { name: '超市老板' },
                        { name: '手机店老板' },
                        { name: '退休职工' },
                        { name: '自由职业' },
                        { name: '学生' },
                        { name: '宝妈' },
                        { name: '务农' },
                        { name: '其他' },
                    ],
                    label: '职业',
                    labelKey: 'job',
                    errorStatus: false,
                    valiedateMessage: '输入不能为空',
                },
                dream: {
                    label: '我的梦想',
                    labelKey: 'dream',
                    placeholder: '小目标还是要有的，万一平台帮你实现了呢~',
                    value: '',
                    required: true,
                    errorStatus: false,
                    valiedateMessage: '输入不能为空',
                },
                declaration: {
                    label: '合伙人宣言',
                    labelKey: 'declaration',
                    placeholder: '请介绍一下自己，不少于50字',
                    length: 50,
                    value: '',
                    required: true,
                    errorStatus: false,
                    valiedateMessage: '输入不能为空',
                },
            },
            joinRemind: config.joinRemind,
        };
    },
    created() {
        this.$root.hidePlaceholder();
        this.$o2o({
            e_c: '申请页曝光',
            e_a: '曝光',
            e_n: '地推合伙人平台',
        });
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
    },
    destroyed() {
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        isShowNavbar() {
            return isXiangkan();
        },
        showAction() {
            this.form.job.show = true;
        },
        backKeyHandler() {
            this.$router.go(-1);
        },
        hanleValidate(value) {
            if (!/^1[0-9]{10}$/.test(value)) {
                return {
                    validate: false,
                    valiedateMessage: '手机号输入有误！',
                };
            }
            return {
                validate: true,
                valiedateMessage: '',
            };
        },
        check() {
            const keys = Object.keys(this.form);
            keys.forEach((item) => {
                if (!this.form[item].value) {
                    this.form[item].errorStatus = true;
                    this.form[item].valiedateMessage = `${this.form[item].label}输入不能为空`;
                }
            });
            if (keys.find(item => this.form[item].errorStatus === true)) {
                return;
            }
            this.submit();
        },
        submit() {
            this.$o2o({
                e_c: '立即提交申请',
                e_a: '点击',
                e_n: '地推合伙人平台',
            });
            const params = {
                name: this.form.name.value,
                telephone: this.form.phone.value,
                career: this.form.job.value,
                dream: this.form.dream.value,
                manifesto: this.form.declaration.value,
                id: 0,
            };
            // 提交信息
            joinPartner(params).then((ret) => {
                this.$router.replace({
                    path: '/act/partner',
                    query: {
                        inviteNo: ret,
                    },
                });
                // 提交信息后的新一步处理
            }).catch((e) => {
                toast(e.msg || '内部错误');
            });
        },
        errorCollect(err, name) {
            this.form[name].errorStatus = err;
        },
        onSelect(item) {
            // 点击选项时默认不会关闭菜单，可以手动关闭
            this.form.job.value = item.name;
            this.form.job.errorStatus = false;
            this.form.job.show = false;
        },
        onCancel() {
            this.form.job.show = false;
        },
    },
};
</script>

